/* Do not remove the comments below. It's the markers used by wiredep to inject
   sass dependencies when defined in the bower.json of your dependencies */
.browsehappy {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0; }

body {
  font-family: 'Helvetica', sans-serif;
  margin: 0;
  background-color: #FA936E; }

a {
  text-decoration: none; }

.container {
  width: 800px;
  margin: 20px auto;
  background-color: #fff; }

.content {
  padding: 10px;
  text-align: center; }

h1, h2, h3 {
  font-weight: normal;
  margin: 0; }

h1 {
  margin-bottom: 10px;
  padding: 10px 0;
  text-align: center;
  font-size: 2.2em;
  background-color: #fcfcfc; }

h2 {
  font-size: 1em;
  margin: 10px 0; }
  h2 i {
    font-style: normal;
    color: #aaa;
    font-size: 0.9em; }

h3 {
  font-size: 0.8em;
  text-transform: uppercase;
  margin: 20px 0 5px; }

.section {
  width: 30%;
  display: inline-block;
  vertical-align: top; }

.trace {
  white-space: normal;
  margin: 5px 0;
  text-align: left;
  background-color: #fcfcfc;
  padding: 6px; }

